
.header {
    background-color: $verde;
    .header__contenido {
        padding: $separacion 0;

        @include responsive($tablet) {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    h1 { //Este h1 pertenece a header__contenido, pero tambi√©n a "header". mejor que no haya un elemento anidado a 3 niveles
        color: $blanco;
        margin-bottom: 1rem;
        text-align: center;
        @include responsive($tablet){
            margin-bottom: 0;
        }
    }
    &.fijo {
        position: fixed;
        width: 100%;
        -webkit-box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.51);
        -moz-box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.51);
        box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.51);  
        background-color: $verde;

        .header__contenido {
            padding: 1rem 0;
        }
    }

}

.navegacion-ppal {
    display: flex;
    justify-content: center;
    a {
        color: $blanco;
        font-size: 2rem;
        margin: 0 1.5rem;
        &:hover { //El "&" hace referencia al elemento padre.
            color: $amarillo;
        }
    }

}



