@mixin responsive($screen) {
    @media (min-width: $screen) { /*Dispositivo Grande*/
        @content;
    }
}

@mixin grid($columnas, $filas, $gap) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    grid-template-rows: repeat($filas, 1fr);
    gap: $gap;
}
@mixin flex($direction, $jstf-cntnt, $aln-itms, $gap) {
    display: flex;
    flex-direction: $direction;
    justify-content: $jstf-cntnt;
    align-items: $aln-itms;
    gap: $gap;
}