.lineup {
    background-color: $rosa;
    padding: 7rem 0;
    .dia {
        margin: 5rem 0;
        text-transform: uppercase;
        font-size: 2rem;
    }
    h3, p {
        text-align: center;
        color: $blanco;
    }
   
    .escenarios-contenedor {
        @include responsive($tablet){
            @include grid(2, 1, $separacion)
        }
    }
    .escenario {
        padding-top: $separacion / 2;
    }
    .nombre-escenario {
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &::after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            margin: 2rem auto 0 auto;
            background-repeat: no-repeat;
            background-size: 15rem;
            background-position: center center;
        }
    } //cierra .nombre-escenario
}
.edm .nombre-escenario {
    &::after {
        background-image: url(../img/icono_edm.png);
    }
}
.rock .nombre-escenario {
    &::after {
        background-image: url(../img/icono_rock.png);
    }
}

.calendario {
    padding: 0;
    list-style: none;
    li {
        padding: $separacion;
        font-weight: $bold;

        span {
            font-weight: $regular;
        }
    }
}
.bg-verde .calendario li:nth-child(2n + 1) {
    background-color: darken($verde, 5);
}
.bg-amarillo .calendario li:nth-child(2n + 1) {
    background-color: darken($amarillo, 10);
}