html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
}
body {
    font-family: $fuente-ppal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden;
    }
}
p {
    color: $negro;
}
.contenedor {
    max-width: 110rem;
    width: 95%;
    margin: 0 auto;
    @include responsive($tv) {
        max-width: 220rem;
    }
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
h1, h2, h3 {
    margin: 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
