html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  color: #000000; }

.contenedor {
  max-width: 110rem;
  width: 95%;
  margin: 0 auto; }
  @media (min-width: 1920px) {
    .contenedor {
      /*Dispositivo Grande*/
      max-width: 220rem; } }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-amarillo {
  background-color: #FEF46D; }

.bg-verde {
  background-color: #4cb8b3; }

.header {
  background-color: #4cb8b3; }
  .header .header__contenido {
    padding: 2.5rem 0; }
    @media (min-width: 860px) {
      .header .header__contenido {
        /*Dispositivo Grande*/
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #FFFFFF;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 860px) {
      .header h1 {
        /*Dispositivo Grande*/
        margin-bottom: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    -webkit-box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.51);
    -moz-box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.51);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.51);
    background-color: #4cb8b3; }
    .header.fijo .header__contenido {
      padding: 1rem 0; }

.navegacion-ppal {
  display: flex;
  justify-content: center; }
  .navegacion-ppal a {
    color: #FFFFFF;
    font-size: 2rem;
    margin: 0 1.5rem; }
    .navegacion-ppal a:hover {
      color: #FEF46D; }

.video {
  position: relative;
  max-height: 34rem;
  overflow: hidden; }
  @media (min-width: 860px) {
    .video {
      /*Dispositivo Grande*/
      max-height: 70rem; } }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(137, 28, 30, 0.8);
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.65) 51%, rgba(255, 255, 255, 0.5) 100%);
    background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(137, 28, 30, 0.8)), color-stop(51%, rgba(158, 55, 242, 0.65)), color-stop(100%, rgba(255, 255, 255, 0.5)));
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.65) 51%, rgba(255, 255, 255, 0.5) 100%);
    background: -o-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.65) 51%, rgba(255, 255, 255, 0.5) 100%);
    background: -ms-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.65) 51%, rgba(255, 255, 255, 0.5) 100%);
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.65) 51%, rgba(255, 255, 255, 0.5) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#891c1e', endColorstr='#ffffff', GradientType=1 ); }
  .video video {
    display: block;
    overflow: hidden; }
    @media (min-width: 860px) {
      .video video {
        /*Dispositivo Grande*/
        width: 100%; } }

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  z-index: 1; }
  .contenido-video h2 {
    color: #FFFFFF;
    display: flex;
    flex-direction: column;
    font-size: 7rem; }
    .contenido-video h2 span {
      background-color: #f53756;
      font-size: 2rem;
      padding: 1rem 2rem;
      width: 100%; }

.sobre-festival {
  padding: 5rem 0; }
  @media (min-width: 860px) {
    .sobre-festival {
      /*Dispositivo Grande*/
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(0, 1fr);
      gap: 2.5rem; } }
  .sobre-festival .sobre-festival__texto {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .sobre-festival h2 {
    margin-bottom: 3rem; }
  .sobre-festival h4 {
    color: #f53756; }
  .sobre-festival p {
    line-height: 1.5; }

.lineup {
  background-color: #f53756;
  padding: 7rem 0; }
  .lineup .dia {
    margin: 5rem 0;
    text-transform: uppercase;
    font-size: 2rem; }
  .lineup h3, .lineup p {
    text-align: center;
    color: #FFFFFF; }
  @media (min-width: 860px) {
    .lineup .escenarios-contenedor {
      /*Dispositivo Grande*/
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(1, 1fr);
      gap: 2.5rem; } }
  .lineup .escenario {
    padding-top: 1.25rem; }
  .lineup .nombre-escenario {
    color: #000000;
    font-size: 2rem;
    font-weight: 300; }
    .lineup .nombre-escenario::after {
      content: '';
      display: block;
      width: 20rem;
      height: 10rem;
      margin: 2rem auto 0 auto;
      background-repeat: no-repeat;
      background-size: 15rem;
      background-position: center center; }

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png); }

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png); }

.calendario {
  padding: 0;
  list-style: none; }
  .calendario li {
    padding: 2.5rem;
    font-weight: 700; }
    .calendario li span {
      font-weight: 400; }

.bg-verde .calendario li:nth-child(2n + 1) {
  background-color: #43a8a3; }

.bg-amarillo .calendario li:nth-child(2n + 1) {
  background-color: #fef03a; }

.galeria {
  padding: 7rem 0;
  text-align: center; }
  .galeria h3 {
    margin-bottom: 5rem; }
  .galeria .thumb-img {
    transition: 200ms; }
    .galeria .thumb-img:hover {
      cursor: pointer;
      transform: scale(1.03); }

.galeria-imagenes {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  gap: 1.25rem;
  list-style: none; }
  @media (min-width: 860px) {
    .galeria-imagenes {
      /*Dispositivo Grande*/
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(1, 1fr);
      gap: 1.25rem; } }
  @media (min-width: 1280px) {
    .galeria-imagenes {
      /*Dispositivo Grande*/
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: repeat(1, 1fr);
      gap: 1.25rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    position: relative; }

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: rgba(255, 255, 255, 0.7);
  display: grid;
  place-items: center;
  font-weight: 900;
  border-radius: 50%;
  position: relative; }
  .btn-cerrar::before, .btn-cerrar::after {
    height: 3rem; }
  .btn-cerrar::before {
    transform: rotate(45deg); }
  .btn-cerrar::after {
    transform: rotate(-45deg); }

.btn-cerrar, .flechas {
  transition: 200ms; }
  .btn-cerrar::before, .btn-cerrar::after, .flechas::before, .flechas::after {
    content: '';
    width: .4rem;
    border-radius: 1rem;
    background-color: #000000;
    position: absolute; }
  .btn-cerrar:hover, .flechas:hover {
    cursor: pointer;
    background-color: #FFFFFF; }

.flechas {
  width: 2.5rem;
  height: 5rem;
  background-color: rgba(255, 255, 255, 0.7);
  display: grid;
  place-items: center;
  font-weight: 900;
  position: absolute;
  top: 35%;
  transform: scale(3); }
  .flechas::before, .flechas::after {
    height: 2rem; }
  @media (min-width: 860px) {
    .flechas {
      /*Dispositivo Grande*/
      width: 5rem;
      transform: scale(1);
      border-radius: 50%; }
      .flechas:hover {
        transform: scale(1.2); } }

.flechaDerecha {
  right: 1rem; }
  .flechaDerecha::before {
    transform: translateY(-5.2px) rotate(-45deg); }
  .flechaDerecha::after {
    transform: translateY(6px) rotate(45deg); }
  @media (min-width: 860px) {
    .flechaDerecha {
      /*Dispositivo Grande*/
      right: 15%; } }

.flechaIzq {
  left: 1rem; }
  .flechaIzq::before {
    transform: translateY(-5.2px) rotate(45deg); }
  .flechaIzq::after {
    transform: translateY(6px) rotate(-45deg); }
  @media (min-width: 860px) {
    .flechaIzq {
      /*Dispositivo Grande*/
      left: 15%; } }

.boletos {
  padding: 7rem 0; }
  .boletos h3, .boletos p, .boletos th {
    color: #FFFFFF;
    text-align: center; }
  .boletos .contenedor-boletos {
    margin-top: 1.25rem; }
    @media (min-width: 480px) {
      .boletos .contenedor-boletos {
        /*Dispositivo Grande*/
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(1, 1fr);
        gap: 2.5rem; } }
  .boletos .plan p {
    font-size: 3rem;
    margin-bottom: 2rem; }

.pase {
  border-spacing: 0;
  width: 100%; }
  .pase thead {
    background-color: #f53756; }
    .pase thead th {
      padding: 0.625rem;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 2rem; }
  .pase tbody {
    background-color: #FFFFFF; }
    .pase tbody td {
      padding: 0.625rem;
      border-bottom: 1px solid #e1e1e1; }
  .pase tr:last-of-type td {
    border: none; }
  .pase tfoot {
    background-color: #752f97; }
    .pase tfoot td {
      padding: 0.625rem;
      color: #FFFFFF;
      text-align: center;
      font-size: 2.5rem;
      font-weight: 300; }

.footer {
  background-color: #f53756;
  padding: 0.625rem;
  margin: 0;
  text-align: center; }
  .footer p {
    color: #FFFFFF; }
