.galeria {
    padding: 7rem 0;
    text-align: center;
    h3 {
        margin-bottom: 5rem;
    }
    .thumb-img {
        transition: 200ms;
        &:hover{
            cursor: pointer;
            transform: scale(1.03);
        }
    }
}
.galeria-imagenes {
    padding: 0;
    @include grid(2, 1, $separacion / 2);
    list-style: none;

    @include responsive($tablet){
        @include grid(4, 1, $separacion / 2)
    }
    @include responsive($desktop) {
        @include grid(6, 1, $separacion / 2)
    }
}

.overlay {
    position: fixed;
    background-color: rgba($color: #000000, $alpha: .5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;

    img {
        position: relative;
    }
}
.btn-cerrar {
    width: 5rem;
    height: 5rem;
    background-color: rgba($color: $blanco, $alpha: .7);
    display: grid;
    place-items: center;
    font-weight: $black;
    border-radius: 50%;
    position: relative;

    //La "X" para cerrar la imagen
    &::before, &::after {
        height: 3rem;
    }
    &::before {
        transform: rotate(45deg);
    }
    &::after {
        transform: rotate(-45deg);
    }
}
.btn-cerrar, .flechas{
    &::before, &::after {
        content: '';
        width: .4rem;
        border-radius: 1rem;
        background-color: $negro;
        position: absolute;
    }
    transition: 200ms;
    &:hover {

        cursor: pointer;
        background-color: $blanco;
    }
}
.flechas {
    width: 2.5rem;
    height: 5rem;
    background-color: rgba($color: $blanco, $alpha: .7);
    display: grid;
    place-items: center;
    font-weight: $black;
    position: absolute;
    top: 35%;
    transform: scale(3);


    &::before, &::after {
        height: 2rem;
    }

    @include responsive($tablet) {
        width: 5rem;
        transform: scale(1);
        border-radius: 50%;
        &:hover {
            transform: scale(1.2);
        }
    }
}
.flechaDerecha {
    right: 1rem;
    &::before {
        transform: translateY(-5.2px) rotate(-45deg)
    }
    &::after {
        transform: translateY(6px) rotate(45deg)
    }
    @include responsive($tablet) {
        right: 15%;
    }
}
.flechaIzq {
    left: 1rem;
    &::before {
        transform: translateY(-5.2px) rotate(45deg)
    }
    &::after {
        transform: translateY(6px) rotate(-45deg)
    }
    @include responsive($tablet) {
        left: 15%;
    }

}