.boletos {
    padding: 7rem 0;

    h3, p, th {
        color: $blanco;
        text-align: center;
    }

    .contenedor-boletos {
        margin-top: $separacion / 2;
        @include responsive($telefono){
            @include grid(2, 1, $separacion)
        }

    }
    .plan {
        p {
            font-size: 3rem;
            margin-bottom: 2rem;
        }
    }
}

.pase {
    border-spacing: 0;
    width: 100%;

    thead {
        background-color: $rosa;
        th{
            padding: $separacion / 4;
            text-transform: uppercase;
            font-weight: $delgada;
            font-size: 2rem;
        }
    }

    tbody {
        background-color: $blanco;

        td {
            padding: $separacion / 4;
            border-bottom: 1px solid #e1e1e1;
        }
    }
    tr {
        &:last-of-type {
            td {
                border: none;
            }
        }
    }

    tfoot {
        background-color: $morado;
        td {
            padding: $separacion / 4;
            color: $blanco;
            text-align: center;
            font-size: 2.5rem;
            font-weight: $delgada;
        }
    }
}