
.sobre-festival {
    padding: 5rem 0;

    @include responsive($tablet){
        @include grid(2, 0, $separacion)
    }

    .sobre-festival__texto {
        display: flex;
        flex-direction: column;
        justify-content: center;
    
    }
    h2 {
        margin-bottom: 3rem;
    }
    h4 {
        color: $rosa;
    }
    p {
        line-height: 1.5;
    }
}
